@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
body {
  margin: 1rem 0 2rem 0;
  font-family: "Noto Sans", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1rem;
}

a {
  /* default anchor styles */
  color: orange;
  padding: 1rem;
}

a.active {
  /* if this link matches the url */
  color: rgb(3, 98, 161);
  font-weight: 600;
}

.navbar {
  font-size: 1.5rem;
}

h1 {
  text-align: center;
  margin: 2rem;
}

.instructions {
  line-height: 2.8rem;
}

.li_title {
  line-height: 1rem;
}

.instructions_list {
  list-style: square;
}

.ul_list {
  line-height: 2rem;
  list-style: none;
  margin-left: 0;
  padding-left: 0;
}

.user_container {
  display: flex;
  margin-top: 2rem;
}

.user_list {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 0.5rem;
}

.user_card {
  width: 250px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.192);
  border-radius: 20px;
  padding: 15px 10px 2px 10px;
  margin: 0;
  background-color: rgba(0, 82, 136, 0.103);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  align-items: center;
  text-align: center;
  color: black;
}

.user_card:hover {
  transform: scale(1.1);
  background-color: rgb(3, 98, 161);
  color: white;
}

.user_info .user_img {
  border-radius: 15px;
}

a {
  text-decoration: none;
}

.user_info {
  font-size: 0.8rem;
}

.user_info_name {
  font-size: 1.1rem;
}

.loader_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
}

.loader {
  width: 50px;
  height: 50px;
  background-color: none;
  border: 5px solid #e9e51d;
  border-radius: 100%;
  border-style: dashed;

  /* animation */
  animation-name: loader;
  animation-timing-function: linear;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}

.loader_text {
  margin: 1.5rem;
}

@keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.user_details_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 3rem;
  background-color: rgba(167, 167, 167, 0.123);
  border-radius: 50px;
  padding: 20px 40px 40px 40px;
}

.user_details img {
  width: 250px;
  height: 250px;
  margin-right: 2rem;
  border-radius: 50%;
}

.user_details {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.user_details_info1 {
  margin-left: 1rem;
}

.user_details_info2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 1.2rem;
}

.user_name {
  font-size: 1.8rem;
  font-weight: 600;
  text-align: center;
  color: rgb(9, 43, 71);
}

.user_cell,
.user_email,
.user_address,
.user_login_title {
  font-size: 1.2rem;
  color: rgb(3, 98, 161);
  font-weight: 600;
}

.user_cell_info,
.user_email_info,
.user_address_info,
.user_login_info {
  color: black;
  font-weight: 400;
}

.user_login {
  line-height: 15px;
}

.user_login_username,
.user_login_password,
.user_login_uuid {
  font-size: 1.1rem;
}

.addressList {
  margin-top: 2rem;
}

.detailLink:hover {
  color: rgb(0, 81, 128);
}

img {
  border-radius: 50%;
}

th.tableHead {
  text-align: center;
  font-size: 1rem;
}

